"use client";

import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import axiosInstance from "@/lib/axiosInstance";
import { JobI } from "../utility/props";


const fetchJobs = async (): Promise<JobI[]> => {
  const res = await axiosInstance.get("/jobs");
  return res.data;
};


const fetchJobById = async (id: number): Promise<JobI> => {
  const res = await axiosInstance.get(`/jobs/${id}`);
  return res.data;
};

const createJob = async (job: Omit<JobI, "id">) => {
  const res = await axiosInstance.post("/jobs", job);
  return res.data;
};

const updateJob = async ({ id, ...data }: JobI) => {
  const res = await axiosInstance.put(`/jobs/${id}`, data);
  return res.data;
};

const deleteJob = async (id: number) => {
  const res = await axiosInstance.delete(`/jobs/${id}`);
  return res.data;
};


export const useJobs = () =>
  useQuery({
    queryKey: ["jobs"],
    queryFn: fetchJobs,
  });

export const useJob = (id: number) =>
  useQuery({
    queryKey: ["jobs", id],
    queryFn: () => fetchJobById(id),
    enabled: !!id,
  });

export const useCreateJob = () => {
  const queryClient = useQueryClient();
  return useMutation({
    mutationFn: createJob,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["jobs"] });
    },
  });
};

export const useUpdateJob = () => {
  const queryClient = useQueryClient();
  return useMutation({
    mutationFn: updateJob,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["jobs"] });
    },
  });
};

export const useDeleteJob = () => {
  const queryClient = useQueryClient();
  return useMutation({
    mutationFn: deleteJob,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["jobs"] });
    },
  });
};
